import $ from 'jquery';
import Vue from 'vue'

var baseUrl = 'http://localhost:31291/';
$.ajaxSetup({
  beforeSend: function (xhr, options) {
    options.url = baseUrl + options.url;
    xhr.setRequestHeader('Authorization', Vue.cookie.get('token'));
  },
  crossDomain: true,
  dataType: 'json'
});

export default {
  $,
  getMessage(data) {
    return $.parseJSON(data.responseText).message;
  }
}



