<template>
  <div class="page-container" style="min-height:443px">

    <!-- Page content -->
    <div class="page-content">

      <!-- Main content -->
      <div class="content-wrapper">

        <!-- Page header -->
        <div class="page-header page-header-default">
          <div class="page-header-content">
            <div class="page-title">
              <h4><a href="index.html"><i class="icon-arrow-left52 position-left"></i></a> <span class="text-semibold">Registration</span>
              </h4>
              <a class="heading-elements-toggle"><i class="icon-more"></i></a></div>
          </div>

        </div>
        <!-- /page header -->


        <!-- Content area -->
        <div class="content col-md-offset-2">
          <form id="register-form" class="form-horizontal col-md-10"
                @submit.prevent="register()">
            <div class="panel panel-flat" style="padding: 0 10%;">
              <div class="panel-heading">
                <h5 class="panel-title">Register</h5>
              </div>

              <div class="panel-body">
                <div class="row">
                  <fieldset>
                    <legend class="text-semibold">Account Details</legend>
                    <div class="form-group">
                      <label class="col-lg-3 control-label">Email:</label>
                      <div class="col-lg-9"
                           :class="{'has-error': errors.has('email')}">
                        <input id="email" name="email" v-validate="'required|email'" placeholder="e.g. name@company.com"
                               class="form-control" v-model="registration.email">
                        <span v-show="errors.has('email')" class="help-block">{{ errors.first('email') }}</span>
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-lg-3 control-label">Confirm Email:</label>
                      <div class="col-lg-9"
                           :class="{'has-error': errors.has('repeat_email')}">
                        <input name="repeat_email" class="form-control"
                               v-validate="'confirmed:email'" data-vv-name="repeat email">
                        <span v-show="errors.has('repeat_email')"
                              class="help-block">{{ errors.first('repeat_email') }}</span>
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-lg-3 control-label">Password:</label>
                      <div class="col-lg-9"
                           :class="{'has-error': errors.has('password')}">
                        <input v-model="registration.password"
                               v-validate="'required|min:6|max:20'"
                               name="password" type="password"
                               class="form-control required">
                        <!--, regex: /([^a-zA-Z0-9])/g }-->
                        <span class="help-block">Passwords must have at least one non letter or digit character, and at least one uppercase</span>
                        <span v-show="errors.has('password') && errors.firstRule('password') != 'regex'"
                              class="help-block">{{ errors.first('password') }}</span>
                      </div>
                    </div>

                    <legend class="text-semibold">Personal Details</legend>

                    <div class="form-group">
                      <label class="col-lg-3 control-label">Your name:</label>
                      <div class="col-lg-9">
                        <div class="row">
                          <div class="col-md-6"
                               :class="{'has-error': errors.has('first_name')}">
                            <input v-model.lazy="registration.FirstName"
                                   v-validate="'required'"
                                   type="text" name="first_name" placeholder="First name"
                                   class="form-control">
                          <span v-show="errors.has('first_name')"
                                class="help-block">{{ errors.first('first_name') }}</span>
                          </div>

                          <div class="col-md-6"
                               :class="{'has-error': errors.has('last_name')}">
                            <input v-model.lazy="registration.LastName"
                                   v-validate="'required'"
                                   type="text" name="last_name" placeholder="Last name"
                                   class="form-control">
                          <span v-show="errors.has('last_name')"
                                class="help-block">{{ errors.first('last_name') }}</span>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-lg-3 control-label">Birthday:</label>
                      <div class="col-lg-9 ">
                        <dateSelector v-model="registration.Birthdate" name="birthday"></dateSelector>
                        <input type="hidden" v-model="registration.Birthdate" name="birthday"
                               v-validate="'required'">
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-lg-3 control-label">Phone:</label>
                      <div class="col-lg-9">
                        <input v-model.lazy="registration.PhoneNumber" type="tel" name="phone"
                               placeholder="e.g. 052-xxx-xxxx"
                               class="form-control">
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-lg-3 control-label">Location:</label>
                      <div class="col-lg-9">
                        <div class="row">
                          <div class="col-md-6">
                            <div class="mb-15">
                              <select v-model="registration.country" name="country"
                                      data-placeholder="Select your country"
                                      class="form-control"
                                      tabindex="-1" aria-hidden="true">
                                <option></option>
                                <option value="il">Israel</option>
                                <option value="us">USA</option>
                              </select>
                            </div>

                            <input v-model="registration.city" name="city" type="text" placeholder="City"
                                   class="form-control">
                          </div>

                          <div class="col-md-6">
                            <input v-model="registration.State" name="state" type="text" placeholder="State/Province"
                                   class="form-control mb-15">
                            <input v-model.lazy="registration.ZipCode"
                                   v-validate="'digits:6'"
                                   name="zip" type="text" placeholder="ZIP code" class="form-control">
                          </div>
                        </div>
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-lg-3 control-label">Address:</label>
                      <div class="col-lg-9">
                        <input v-model="registration.address" name="address" type="text"
                               placeholder="e.g. Hankin 13, Tel Aviv"
                               class="form-control">
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-lg-3 control-label">Website:</label>
                      <div class="col-lg-9"
                           :class="{'has-error': errors.has('website')}">
                        <input v-model.lazy="registration.Website" placeholder="e.g. www.myCompamy.com"
                               v-validate:registration.Website="'url'" name="website"
                               class="form-control">
                        <span v-show="errors.has('website')" class="help-block">{{ errors.first('website') }}</span>
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-lg-3 control-label">Additional info:</label>
                      <div class="col-lg-9">
                      <textarea v-model="registration.AdditionalInfo" name="additional_details" rows="5" cols="5"
                                class="form-control"
                                placeholder="Share something about youself"></textarea>
                      </div>
                    </div>

                    <div class="checkbox">
                      <label>
                        <input v-model.lazy="registration.agree" name="agree" type="checkbox"
                               v-validate="'required'" class="required"
                               :class="{'has-error': errors.has('agree')}">
                        <span>* I accept the <a href="#">Terms</a> and Conditions</span>
                      </label>
                    </div>
                    <div class="has-error" v-show="errors.has('agree')">
                      <span class="help-block">you must agree to the terms first</span>
                    </div>
                    <div class="checkbox">
                      <label>
                        <input v-model="registration.SendPersonalizedInfo" type="checkbox" checked="checked">
                        I want to receive personalized offers by your site
                      </label>
                    </div>
                  </fieldset>
                </div>

                <div class="text-right">
                  <button type="submit" class="btn btn-primary">Register <i
                    class="icon-arrow-right14 position-right"></i></button>
                </div>

                <div class="has-error" v-show="errors.any()">
                  <div class="alert alert-danger help-block">
                    <span>some field still have errors:</span>
                    <ul>
                      <li v-for="error in errors.all()">{{error}}</li>
                    </ul>
                  </div>
                </div>

              </div>
            </div>
          </form>
        </div>
        <!-- /content area -->

      </div>
      <!-- /main content -->

    </div>
    <!-- /page content -->

  </div>
</template>

<script>
  import dateSelector from '@/components/util/dateSelector'
  import loginService from '../../services/loginService.js'

  export default {
    name: 'Register',
    data () {
      return {
        registration: {
          email: '',
          password: '',
          FirstName: '',
          LastName: '',
          Birthdate: null,
          PhoneNumber: '',
          country: '',
          State: '',
          city: '',
          ZipCode: '',
          address: '',
          Website: '',
          AdditionalInfo: '',
          agree: false,
          SendPersonalizedInfo: false
        }
      }
    },
    methods: {
      register: function () {
        var self = this;
        this.$validator.validateAll().then(function (result) {
          if (!self.$validator.errorBag.any()) {
            loginService.register(self.registration, function () {
              alert('registed!');
            }, function (serverError) {
              self.$validator.errorBag.add('serverError', serverError)
            });
          }
        });
      }
    },
    components: {dateSelector}
  }
</script>



