import baseService from './baseService'
import Vue from 'vue'

export default {
  register: function (data, successCB, failCB) {
    data.confirmPassword = data.password;
    baseService.$.post('/api/Account/Register', data, function (data) {
      successCB(baseService.getMessage(data));
    })
      .fail(function (xhr, status, error) {
        console.log(xhr);
        failCB(baseService.getMessage(xhr));
      })
  },
  login: function (data, successCB, failCB) {
    data.grant_type = 'password';
    baseService.$.post('/Token', data, function (data) {
      console.log(data);
      Vue.cookie.set('token', data.access_token, {expires: data.expires_in + 's'})
      if (successCB) {
        successCB(data.access_token);
      }
    })
      .fail(function (xhr, status, error) {
        console.log(xhr);
        if (failCB) {
          failCB(xhr.responseJSON.error_description);
        }
      })
  }
}



